.card__rating-stars {
  display: flex;
}

.card__rating-star {
  background-image: url(/src/images/star.svg);
  width: $star-size;
  height: $star-size;
  background-repeat: no-repeat;
  background-position: center;
  margin-right: 4px;

  &:nth-child(-n + 4) {
    background-image: url(/src/images/star-active.svg);
  }
}
